.styled-ul li {
  padding-left: 57px;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 15px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background-image: url('/one-pager/list-icon.svg');
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.hover\:bg-white\/10:hover {
